/* -------- CSS Reset ---------- */
/* .                             */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

:focus {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: normal;
}

p {
  margin: 0;
  padding: 0;
  line-height: 1;
};

input,
textarea,
button,
select {
  appearance: none;
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}

img {
  border: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
